
button {
  margin: 10px;
  grid-column: 2;
  border-radius: 100px;
  border: 0;
  padding: 7px 20px;
  background-color: #E14D2A;
  color: white;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}

.NavBar {
  background-color: #F9D949;
  padding: 3%;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
